.carousel{
    position: relative;
}

.slider{
    width: 100%;
    height: 100%;
}
.slider .carousel-hero{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    opacity: 0;
    z-index: 0;
    transition: all 1s;
}

.slider img:nth-child(1){
    opacity: 1;
    z-index: 1;
}
.overlay{
    position: absolute;
    top: 35vh;
    z-index: 999;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;
}

.overlay1{
    position: absolute;
    top: 60vh;
    z-index: 999;
    background-color: rgba(255, 255, 255, 0.6);
    padding: 1rem;
    width: 60%;
    border-bottom: 1rem rgba(235, 231, 4, 0.5) solid;
    border-radius: 1rem;
}
.overlay .cards{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.overlay .card{
    border-bottom: 0.5rem #05386b solid;
}

.overlay .cards .card img{
    height: 30vh;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}